body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background: oldlace;
}

.carousel-inner>.item>a>img, .carousel-inner>.item>img {
  margin: 0 auto;
}

.page-main {
  display: -webkit-flex;
  display: flex;
  position: relative;
}
.constructor-area {
  padding-left: 200px;
  max-width: 100%;
  width: 100vw;
  height: 100vh;
}
.ctr-area-inner {
  width: 100%;
  height: 100%;
}
.elements-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 200px;
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  padding: 10px 10px 10px 0;
  background: papayawhip;
  box-shadow: 5px 0px 10px 0px rgba(0, 0, 0, 0.2);
}
.element-list {
  cursor: pointer;
  padding: 5px 10px;
  background: rgb(44, 147, 243);
  color: white;
  border-radius: 4px;
  margin-bottom: 5px;
}
